<template>
    <div class="row">
        <div class="col-12 col-lg-4" v-for='work in latest' :key='work.id'>
            <div class="flip portfolio-item">
                <div class="edit" v-if='User'>
                    <router-link :to="{ name: 'edit_work', params: { title: friendlyUrl(work.title), id: work.id }}">
                        <img src="../blog/icons/edit-regular.svg" alt="Edit">
                    </router-link>
                    
                </div>
                <div class="front" :style="{'background-image': `url(${work.img})`}">
                   <h5 class="blog-work__title">{{work.title}}</h5>
                </div>
                <div class="back" :style="{'background-image': `url(${work.img})`}">
                    <div class="portfolio-item__links">
                        <router-link title='Дивитись деталі' class="portfolio-item__link" tag='span' :to="{ name: 'single-work', params: { title: friendlyUrl(work.title),id: work.id }}">
                          Дивитись деталі
                        </router-link>
                        <a title="Переглянути проект" :href="work.url" class="portfolio-item__link">
                         Переглянути проект
                        </a>
                    </div>
                </div>
            </div>
        </div>
        <!-- /.col-12 col-lg-4 -->
    </div>
    <!-- /.row -->
</template>
<script>
    import { db } from '@/main';
    import cyrillicToTranslit from 'cyrillic-to-translit-js';

    export default {
        name: "LatestWorks",
        data() {
            return {
                works: [],
            }
        },
        methods: {
            friendlyUrl(value) {
                return cyrillicToTranslit({ preset: "uk" }).transform(value.toLowerCase(), "_")
            },
        },
        computed: {
            latest() {
                return this.works.slice(0, 3);
            },
            User() {
                return this.$store.getters.User;
            }
        },
        firestore () {
            return {
                works: db.collection('works').orderBy('createdAt')
            }
        },
    };
</script>
